<template>
  <div>
    <v-app-bar mobileBreakpoint="sm" app dark flat color="primary">
      <v-app-bar-nav-icon
        dark
        class="hidden-md-and-up"
        @click.stop="drawer = !drawer"
      ></v-app-bar-nav-icon>

      <v-tabs dark center-active centered class="hidden-sm-and-down">
        <v-tab @click="$router.push('/')">首页</v-tab>
        <v-tab
          v-for="item in categoryList"
          :key="item.id"
          text
          @click="goCategory(item.id)"
          class="hidden-sm-and-down"
          >{{ item.name }}</v-tab
        >
      </v-tabs>

      <v-spacer></v-spacer>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" color="primary" dark app temporary>
      <v-list>
        <v-list-item-title>
          <v-btn href="/" dark text>
            <v-icon small>mdi-home</v-icon>首页
          </v-btn>
        </v-list-item-title>
        <v-list-item-group v-model="group">
          <v-list-item
            v-for="item in categoryList"
            :key="item.id"
          >
            <v-list-item-content>
              <v-btn dark text @click="goCategory(item.id)" color="white">{{
                item.name
              }}</v-btn>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>
<script>
export default {
  data() {
    return {
      categoryList: [],
      drawer: false,
      group: null,
    };
  },
  watch: {
    group() {
      this.drawer = false;
    },
  },
  created() {
    this.GetCategoryList();
  },
  methods: {
    // 获取分类
    async GetCategoryList() {
      const { data: res } = await this.$http.get("category");
      this.categoryList = res.data;
    },
    goCategory(cid) {
      this.$router.push(`/category/${cid}`);
    },
  },
};
</script>
<style></style>
